html {
  height: 100%;
}

#root {
  min-height: 100%;
  display: grid;
  grid-template-rows: 50px auto auto;
}

header {
  background-color: #f14668;
  text-align: center;
  border-bottom: 5px solid #772233;
}

header h1 {
  height: 50px;
  font-weight: bolder;
  font-size: 25px;
  color: #fff;
}

main {
  background-color: #fff;
  margin: 10px auto;
  width: 380px;
  padding-bottom: 50px;
}

main .add-strategy {
  margin: 10px;
  float: right;
  box-shadow: 1px 1px 3px #aaa;
}

main .strategy {
  display: grid;
  grid-template-columns: [row1-start] 92% [c1-end c2-start] 8% [c2-end];
  justify-content: center;
  text-align: center;
  margin: 10px 0;
  border-radius: 8px;
  border: 2px solid #bbb;
  background-color: #eee;
  box-shadow: 1px 1px 3px #aaa;
}

.strategy .fertilizer-form {
  display: inline-flex;
  width: 100%;
  margin: 0;
  padding-left: 5px;
}

.strategy .fertilizer-field {
  display: inline-block;
  padding: 20px 0 5px 3px;
}

.strategy .delete {
  margin: auto;
}

.strategy .summary-result {
  border-top: 2px solid #bbb;
  padding-top: 10px;
  grid-column: 1 / span 2;
}

.strategy .summary-result var {
  font-style: normal;
  font-weight: bold;
  color: black;
  margin-left: 10px;
  margin-right: 2px;
}

.strategy .strategy-actions {
  padding: 10px;
  grid-column: 1 / span 2;
}

.strategy-actions .buttons {
  float: right;
}

.fertilizer-field {
  width: 60px;
}
